import classes from "./DropDown.module.css" 
import './DropDownEffect.css'

import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const DropDown = props => {
    const cls = ['dropHead']

    if (props.open)
        cls.push('opened')

    return(
        <li className={classes.DropDown}> 
            <p className={cls.join(' ')} onMouseEnter={() => props.setOpen(true)} onMouseLeave={() => props.setOpen(false)} >
                {props.name}
                {props.icon}
            </p>
            
            <AnimatePresence>
            { 
                props.open && 
                <motion.div
                    initial={{x: -30, opacity: .2}}
                    animate={{x: 0, opacity: 1}}
                    exit={{x: -30, opacity: .01}} 
                    onMouseEnter={() => props.setOpen(true)} onMouseLeave={() => props.setOpen(false)}
                >
                    {props.children}
                </motion.div> 
            }
            </AnimatePresence>
        </li>
    )
}

export default DropDown