import classes from './Dialogs.module.css'
import userImg from '../../img/user.png'

import ScrollableFeed from 'react-scrollable-feed'
import { motion } from 'framer-motion';
import { Routes, Route, NavLink } from 'react-router-dom';
import { render } from '@testing-library/react';

import {ReactComponent as SendIcon} from '../../img/send.svg'

const dialogs = [
    {name: ' ..', to: 'a-dialog', messages: [{user: 'Hi!'}, {user: 'How are u'}, {teacher: 'i`m fine! And what about you?'}, {user: 'That`s awesome! Do u have any plans for tomorrow?'}, {teacher: 'Yea, sorry :('}, {user: 'Hi!'}, {user: 'How are u'}, {teacher: 'i`m fine! And what about you?'}, {user: 'That`s awesome! Do u have any plans for tomorrow?'}, {teacher: 'Yea, sorry :('}]},
    {name: ' ..', to: 'b-dialog',  messages: [{user: 'Hi!'}, {teacher: 'Hello'}]},
    {name: ' ..', to: 'c-dialog', messages: [{user: 'Hi!'}, {teacher: 'Hello'}]},
    {name: ' ..', to: 'd-dialog', messages: [{user: 'Hi!'}, {teacher: 'Hello'}]},
    {name: ' ..', to: 'e-dialog', messages: [{user: 'Hi!'}, {teacher: 'Hello'}]},
    {name: ' ..', to: 'f-dialog', messages: [{user: 'Hi!'}, {teacher: 'Hello'}]},
]

const renderDialogs = () => {
    return dialogs.map((item, index) => {
        return(
            <NavLink
                key={index}
                className={({isActive})=>isActive ? classes.d_activeLink : classes.d_item}
                to={item.to}
            >
                <img src={userImg} />
                {item.name}
            </NavLink>
        )
    })
}

const renderMessagesBlock = () => {
    return dialogs.map((item, index) => {
        return(
            <Route
                key={index} 
                className={classes.d_message}
                path={item.to}
                element={<ScrollableFeed>{returnMessages(item)}</ScrollableFeed>}
            />
        )
    })
}

const returnMessages = item => {
    return item.messages.map((message, key) => {
        return(
            <div 
                key={key}
                className={message["user"] ? classes.user_message : classes.teacher_message}
            >   
                <p>{message["user"] || message["teacher"]}</p>
            </div>
        )
    })
}

const Dialogs = props => {
    return(
        <motion.div className={classes.Dialogs}
            initial={{ opacity: 0,  scale: .7 }}
            animate={{ opacity: 1,  scale: 1 }}
            transition={{duration: .5}}
        >
            <h2></h2>

            <div className={classes.Dialogs_block}>
                <div>
                    {renderDialogs()}
                </div>

                <div className={classes.Dialogs_messages_block}>
                    <div className={classes.Dialogs_messages}>
                        <Routes>
                            {renderMessagesBlock()}
                        </Routes>
                    </div>

                    <div className={classes.Dialogs_input}>
                        <input 
                            placeholder=' ...' 
                            // onChange={event => message = event.target.value} 
                        />

                        <motion.i 
                            whileTap={{scale: .9}}
                            // onClick={() => showMessage()}
                        >
                            <SendIcon className={classes.iconSend} />
                        </motion.i>
                    </div>
                </div>

            </div>
        </motion.div>
    )
}

export default Dialogs