import React from "react";
import classes from './Button.module.css'
import { motion } from "framer-motion";

const Button = props => {
    const cls = [
        classes.Button,
        classes[props.type]
    ]


    return(
        <motion.button
            whileTap={{scale: .95}}
            
            className={cls.join(' ')}
            onClick={props.onClick}
            disabled={props.disabled}
        >
            {props.children}
        </motion.button>
    )
}

export default Button