import './TimeTableUI.css'

const TimeTableUI = props => {

    const renderPairs = () => {
        return props.pair.map((item, index) => {
            return(
                <div key={index} className='pairBlock'>
                    <div className='pairInfo'>
                        <p className='paitInfoItem'>{item.num} {item.type}</p>
                        <p className='pairInfoTime'>{item.time}</p>
                    </div>
                    <div className='pairSubject'>
                        <p>{item.subj}</p>
                        <p className='teacherInfo'>{item.teacher}</p>
                    </div>
                </div>
            )
        })
    }

    return(
        <div 
            className='TimeTableUI'
        >
            <div className='tt-date'>
                {props.date}
            </div>
            <div className='tt-pairs'>
                { renderPairs() }
            </div>
        </div>
    )
}

export default TimeTableUI