import { AnimatePresence, LayoutGroup, motion } from "framer-motion";
import React from "react";
import Accordion from "../../UI/Accordion/Accordion";
import './News.css'

const news = [
    { 
        date: "11.05.2022",
        head: "       ",
        text: `          '    '.  ,        .          '    '.  ,        .`
    },
    { 
        date: "23.12.2021",
        head: "     MATLAB    ",
        text: " !      MATLAB    .    MATLAB   /   ."
    },
    { 
        date: "30.04.2019",
        head: "       ",
        text: `          '    '.  ,        .          '    '.  ,        .`
    },
    { 
        date: "01.08.2018",
        head: "     MATLAB    ",
        text: " !      MATLAB    .    MATLAB   /   ."
    },
]

export default function News(){
    const renderNews = () => {
        return news.map((newsItem, index) => {
            return(
                <Accordion
                    key={index}
                    date={newsItem.date}
                    head={newsItem.head}
                >
                    {newsItem.text}
                </Accordion>
            )
        })
    }

    return(
        <div className="News">
            <h2></h2>

            <motion.div
                initial={{ opacity: 0,  scale: .7 }}
                animate={{ opacity: 1,  scale: 1 }}
                transition={{duration: .5}}
            >
                { renderNews() }
            </motion.div>
        </div>
    )
}