class TimeTable extends Component {
    
    state = {
        currentWeek: 1,
        weeks: [
            {week: 1},
            {week: 2},
            {week: 3}
        ]
    }

    renderTimeTable = () => {
        return timetable.map((item, index) => {
            return(
                <div key={index}>
                    <AnimatePresence>
                        { item.week === this.state.currentWeek ? 
                            <TimeTableUI 
                                pair={item.pair}
                                date={item.date}
                            /> :
                            null
                        }
                    </AnimatePresence>
                </div>
            )
        })
    }
    
    renderFilter = () => {
        return this.state.weeks.map((item, index) => {
            return(
                // <p 
                // key={index} 
                // className='TimeTableFilter_item'
                // onClick={() => this.filterWeeks(item.week)}
                // >
                //     {item.week}
                // </p>
                <FilterButtons 
                    key={index}
                    week={item.week}
                    check={this.state.currentWeek}
                    onClick={() => this.filterWeeks(item.week)} 
                />
            )
        })
    }
    
    filterWeeks = week =>{
        this.setState({
            currentWeek: week
        })
    }

    render(){
        return(
            <div className='TimeTable'>
                <h2></h2>
                
                <motion.div
                    initial={{ opacity: 0,  scale: .7 }}
                    animate={{ opacity: 1,  scale: 1 }}
                    transition={{duration: .5}}
                >
                    <div className='TimeTableFilter'>
                        { this.renderFilter() }
                    </div>

                    <div className='TimeTableBlock'>
                        { this.renderTimeTable() }
                    </div>
                </motion.div>
            </div>
        )
    }
}

export default TimeTable