import classes from './Header.module.css'
import logo from '../../img/logo.png'
import {ReactComponent as DownIcon} from '../../img/down.svg'
import {ReactComponent as ProfileIcon} from '../../img/profile.svg'
import DropDown from "./UI/DropDown/DropDown"
import ProfileDropDown from './UI/ProfileDropDown/ProfileDropDown'
import Button from '../../UI/Button/Button'

import React, { useState } from "react"
import { Link, NavLink } from "react-router-dom"
import { motion } from 'framer-motion';

const links = [
    {to: '/time-table', label: ''},
    {to: '/learn-info', label: ''},
    {to: '/2', label: ' '},
    {to: '/dialogs', label: ''}
]

const sublinks = [
    {to: '/news', label: ''},
    {to: '/main', label: ''},
    {to: '/time-table', label: ''},
    {to: '/learn-info', label: ''},
    {to: '/notfound', label: ' '},
    {to: '/8', label: ''},
    {to: '/9', label: ' '},
    {to: '/0', label: ''},
    {to: '/11', label: ' '},
    {to: '/10', label: ''},
    {to: '/12', label: ''},
    {to: '/13', label: ' '},
    {to: '/14', label: 'e'}
]

const Header = props => {

    const[open, setOpen] = useState(false);
    const[openProfile, setOpenProfile] = useState(false);

    const renderLinks = () => {
        return links.map((link, index) => {
            return(
                <motion.li className={classes.linksHolderLi} whileTap={{scale: .9}} key={index}>
                    <NavLink 
                        activeclassname='active' 
                        to={link.to} 
                        className={classes.navBarItems}
                    >
                        { link.label }
                    </NavLink>
                </motion.li>
            )
        })
    }

    const renderSublinks = () => {
        return sublinks.map((sublink, index) => {
            return(
                <NavLink 
                    key={index}
                    className={classes.subLinkOff}
                    activeclassname='active'
                    to={sublink.to} 
                    onClick={() => setOpen(false)}
                >
                    { sublink.label }
                </NavLink>
            )
        })
    }

    const clickHandler = () => {
        props.onClick()
        setOpenProfile(false)
    }

    return(
        <div className={classes.Header}>
            <div className={classes.HeaderLeft}>
                <Link to="/">
                    <motion.img whileTap={{scale: .9}} src={logo} alt="logo" /> 
                </Link>

                { props.authStatus ? 
                    <ul>
                        <DropDown name={''} icon={<DownIcon />} open={open} setOpen={setOpen} >      
                            { renderSublinks() }
                        </DropDown>
                        
                        {renderLinks()}
                    </ul>
                 : null }
            </div>
            
            <ProfileDropDown 
                name={props.profile.name.nameInfo} 
                icon={<ProfileIcon />} 
                openProfile={openProfile} 
                setOpenProfile={setOpenProfile} 
                authStatus={props.authStatus}
            >
                    <Link to="/profile" onClick={() => setOpenProfile(false)}> 
                        <ul>
                            <p className={classes.profileInfo}>{props.profile.name.fieldInfo}: </p>
                            <li>{props.authStatus ? props.profile.name.nameInfo : '...'}</li>
                            <p className={classes.profileInfo}>{props.profile.phone.fieldInfo}: </p>
                            <li>{props.authStatus ? props.profile.phone.phoneInfo : '...'}</li>
                            <p className={classes.profileInfo}>{props.profile.group.fieldInfo}: </p>
                            <li>{props.authStatus ? props.profile.group.groupInfo : '...'}</li>
                            <p className={classes.profileInfo}>{props.profile.num.fieldInfo}: </p>
                            <li>{props.authStatus ? props.profile.num.numInfo : '...'}</li>
                        </ul>
                        <DownIcon />
                    </Link>

                    <Button
                        type="LogOut"
                        onClick={clickHandler}
                    >
                        
                    </Button>
            </ProfileDropDown>
        </div>

    )
}

export default Header
