import './Link.css'
import { motion } from 'framer-motion';

const Link = props => {
    return(
        <div className='Link'>
            <motion.a
                href="#"
                whileTap={{scale: .99}}
            >
                <img src={props.img} />
                <p>{props.text}</p>
            </motion.a>
        </div>
    )
}

export default Link