import Header from './components/Header/Header'
import Footer from './components/Footer/Footer'
import NotFound from './containers/404/404'
import News from './containers/News/News'
import MainPage from './containers/MainPage/MainPage'
import Auth from './containers/Auth/Auth'
import Dialogs from './containers/Dialogs/Dialogs'

import './App.css'

import React, { Component } from "react";
import { Routes, Route, NavLink, useLocation } from "react-router-dom"
import TimeTable from './containers/TimeTable/TimeTable'

class App extends Component {
  
  state = {
    isAuthorised: false, ///////////////
    authFail: true,
    loginData: {
      id: 0,
        name: {nameInfo: '...', fieldInfo: " "},
        group: {groupInfo: '...', fieldInfo: ""}, 
        num:  {numInfo: '...', fieldInfo: "  "},
        login: '...', 
        password: '...', 
        phone: {phoneInfo: '...', fieldInfo: " "},
    },
    profiles: [
      {
        id: 1,
        name: {nameInfo: ' ', fieldInfo: " "},
        group: {groupInfo: '-20-01', fieldInfo: ""}, 
        num:  {numInfo: '277708', fieldInfo: "  "},
        login: 'admin@gmail.com', 
        password: '1234', 
        phone: {phoneInfo: '+7-999-999-99-85', fieldInfo: " "},
      },
      {
        id: 2,
        name: {nameInfo: ' ', fieldInfo: " "},
        group: {groupInfo: '-19-01', fieldInfo: ""}, 
        num:  {numInfo: '178895', fieldInfo: "  "},
        login: 'user@gmail.com', 
        password: '2468', 
        phone: {phoneInfo: '+7-987-123-45-85', fieldInfo: " "},
      },
    ]
  }

  authLogIn = (login, password) => {
    const profilesData = {...this.state.profiles}
    var custoStopFlag = false

    this.state.profiles.map((profile) => {
      if (profile.login === login && profile.password === password){
        this.setState({
          authFail: true,
          loginData: profile,
          isAuthorised: true
        })
        custoStopFlag = true
      }
      else
        if (!custoStopFlag)
          this.setState({
            authFail: false
          })
    })
  }

  authLogOut = () => {
    this.setState({
      isAuthorised: false
    })
  }

  render(){     
    return (
      <div className='wrapper'>
          <Header 
            authStatus={this.state.isAuthorised} 
            profile={this.state.loginData} 
            onClick={this.authLogOut} 
          />
        
          <div className='content'>
              {
                this.state.isAuthorised ? 
                <Routes>
                  <Route path="/" element={<h1>Home</h1>} />
                  <Route path="/news" element={<News />} />
                  <Route path="/profile" element={<h1>Profile</h1>} />
                  <Route path="/notfound" element={<NotFound />} />
                  <Route path="/main" element={<MainPage />} />
                  <Route path="/time-table" element={<TimeTable />} />
                  <Route path="/dialogs/*" element={<Dialogs />} />
                </Routes>:
                <Auth 
                  authStatus={(login, password) => this.authLogIn(login, password)} 
                  authFail={this.state.authFail}
                />
              }
          </div>
        
        <Footer />       
      </div>
    );
  }
}

export default App;
