import classes from "./ProfileDropDown.module.css"
import './ProfileDropDownEffect.css'
import BackDrop from "../BackDrop/BackDrop";

import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const ProfileDropDown = props => {

    const cls = ['dropHeadProfile']

    if (props.openProfile)
        cls.push('openedProfile')

    return(
        <React.Fragment>
            <div className={classes.ProfileDropDown}> 
                <motion.p whileTap={{scale: .9}} className={cls.join(' ')} onClick={() => props.setOpenProfile(!props.openProfile)} >
                    {props.icon}
                    {props.authStatus ? props.name : " "}
                </motion.p>
                <AnimatePresence>
                    { 
                        props.openProfile && 
                        <motion.div
                            initial={{x: -30, opacity: .2}}
                            animate={{x: 0, opacity: 1}}
                            exit={{x: -30, opacity: .01}} 
                        >
                            {props.children}
                        </motion.div>
                    }
                </AnimatePresence>
            </div>

            { props.openProfile ? <BackDrop onClick={() => props.setOpenProfile(false)} /> : null }
        </React.Fragment>
    )
}

export default ProfileDropDown