import React, {Component} from "react";
import is from "is_js";

import Input from "../../UI/Input/Input";
import Button from '../../UI/Button/Button'

import './Auth.css'
import { motion } from 'framer-motion';

class Auth extends Component {
    state = {
        isFormValid: false,
        inputs: {
            login: {
                type: 'login', 
                head: "E-mail", 
                placeholder: ' ', 
                errorMessage: '  e-mail',
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    email: true
                }
            },
            password: {
                type: 'password', 
                head: "", 
                placeholder: ' ', 
                errorMessage: '  ',
                value: '',
                valid: false,
                touched: false,
                validation: {
                    required: true,
                    minLength: 3
                }
            }
        }
    }

    onChangeHandler = (event, inputName) => {
        const inputs = { ...this.state.inputs }
        const input = { ...inputs[inputName] }
    
        input.value = event.target.value
        input.touched = true
        input.valid = this.validateControl(input.value, input.validation)
    
        inputs[inputName] = input
    
        let isFormValid = true

        Object.keys(inputs).forEach(name => {
            isFormValid = inputs[name].valid && isFormValid
        })

        this.setState({
            inputs, isFormValid,
        })
    }

    validateControl(value, validation) {
        if (!validation) 
            return true
        
        let isValid = true
        
        if (validation.required)
            isValid = value.trim() !== '' && isValid  // trim()  
        
        if (validation.email)
            isValid = is.email(value) && isValid

        if (validation.minLength)
            isValid = is.above(value.length, validation.minLength) && isValid

        return isValid
    }

    renderInputs = () => {
        return Object.keys(this.state.inputs).map((inputName, index) => {
            const input = this.state.inputs[inputName]

            return(
                <Input 
                    key={index}
                    type={input.type} 
                    head={input.head}
                    placeholder={input.placeholder}
                    value={input.value}
                    valid={input.valid}
                    errorMessage={input.errorMessage}
                    touched={input.touched}
                    shouldValidate={!!input.validation}
                    onChange={event => this.onChangeHandler(event, inputName)}
                />
            )
        })
    }

    render() {
        return(
            <motion.div className="Auth"
                initial={{ opacity: 0,  scale: .7 }}
                animate={{ opacity: 1,  scale: 1 }}
                transition={{duration: .5}}
            >
                <h2></h2>

                <div className="AuthBody">
                    {this.props.authFail ? null :
                    <div style={{color: 'red', marginTop: '10px'}}>   </div>
                    }

                    {this.renderInputs()}

                    <Button 
                        type="AuthButton" 
                        
                        onClick={
                            () => this.props.authStatus(
                                this.state.inputs.login.value, 
                                this.state.inputs.password.value
                            )}
                        disabled={!this.state.isFormValid}
                    >
                        
                    </Button>
                </div>
            </motion.div>
        )
    }
}

export default Auth
