import React, {useState} from "react";
import './Accordion.css'
import {ReactComponent as DownIcon} from '../../img/down.svg'
import { motion, AnimatePresence } from "framer-motion";

const Accordion = props => {
    const[show, setShow] = useState(false);

    const cls = ['top']
    const svgCls = ['acordion-icon']

    if (show) {
        cls.push('top-opened')
        svgCls.push('icon-closed')
    }

    return(
        <div className="Accordion">
            <div className={cls.join(' ')} onClick={() => setShow(!show)}>
                <div>
                    <p className="date">
                        {props.date}
                    </p>
                    <p className="head">
                        {props.head}
                    </p>
                </div>
                <DownIcon className={svgCls.join(' ')} />
            </div>
            <AnimatePresence>
            {
                show &&
                <motion.div
                    initial={{ opacity: 0, height: 0, scale: .99 }}
                    animate={{ opacity: 1, height: "auto", scale: 1 }}
                    exit={{ opacity: 0, height: 0, scale: .99 }}
                    transition={{ duration: .2 }}
                    className="text"
                >   
                    <div>
                        {props.children}
                    </div>
                </motion.div>
            }
            </AnimatePresence>           
        </div>
    )
}

export default Accordion